.reelsboxbody{
    height:100vh;
    width: 100%;
    padding: 70px 15px 0px 100px;
    box-sizing: border-box;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;

}
.reelsup,.reelsdown{
    position: absolute;
    height: 50px;
    aspect-ratio: 1;
    background: rgb(50, 54, 57);
    border-radius: 50%;
    border: none;
    right: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
}

.reelsdown{
bottom: 20px;
}

.reelsup:hover,.reelsdown:hover{
    background: rgb(100, 100, 100);
}

.reelsscroll{
    transition: 1s;
    height: 100%;
    aspect-ratio: 11/16;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* overflow: hidden; */
}


@media screen and (max-width:600px) {
    .reelsboxbody{
    padding: 70px 15px 0px 15px;
    }
    }