.Createvideohome{
    
    height:100vh;
    width: 100%;
    background: black;
    color: white;
    padding: 70px 15px 20px 95px;
    box-sizing: border-box;
    overflow: hidden;
    overflow-y: scroll;
}
.preview{
   display: flex;
   gap: 15px;
   width: 100%;
}
.videopreview ,.imagepreview {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 10px;
}
.videopreview video,.imagepreview img{
    border: 2px solid white;
    background: linear-gradient(150deg,red,yellow);
    width: 100%;
    aspect-ratio: 16/9;
}

.details{
    margin-top: 15px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.details input{
    border-radius: 10px;
    text-indent: 20px;
    height: 30px;
    font-size: 15px;
    outline: none;
}
.selectvideotype{
    display: grid;
    padding-left: 40px;
    margin: auto;
    min-width: 300px;
    justify-self: center;
    grid-template-columns: auto auto;
}
.details button{
    border: none;
    border-radius: 10px;
    font-size: 15px;
    height: 30px;
    width: 200px;
    align-self: center;
}
.details button:hover{
    background: rgb(0, 162, 255);
    color: white;
}

@media screen and (max-width:600px) {
     
    .Createvideohome{
    padding: 65px 15px 20px 15px;
    overflow-y: scroll;
    }
    .preview{
        flex-direction: column;
    }

}