.sidemenu{
    min-width: 70px;
    width: 70px;
    height: 1000px;
    background: rgb(0, 0, 0);
    position:fixed;
    top: 50px;
    z-index: 10;
    color: white;
    padding: 10px 5px;
}
.sidemenu button{
    width: 100%;
    height: 80px;
    background: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: none;
    border-radius: 10px;
    cursor: pointer;
}
.sidemenu button:hover{
    background: rgb(50, 54, 57);
}
.sidemenu svg{
    transform: scale(0.6);
}
.sidemenu button p{
    color: white;
    font-size: 10px;
    transform: translateY(-15px);
}
@media screen and (max-width:600px) {
.sidemenu{
    display: none;
}
}