        .navbar{
                display: flex;
                width: 100%;
                height: 50px;
                background: rgb(0, 0, 0);
                box-sizing: border-box;
                position: fixed;
                top: 0;
                z-index: 10;
        }
        .leftnav{
             min-width: 200px;
             width: 200px;
             display: flex;
             align-items: center;
             padding: 0 20px;
             overflow: hidden;
        }
        #logo{
            height: 80px;
            transform: translateY(2px);
        }
        .centernav{
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 10px;
            padding: 0px 20px;

        }
        .searchbox{
            border: 1px solid rgb(70, 70, 70);
            height: 35px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 40px;
            overflow: hidden;
            width: 100%;
            max-width: 600px;
        }
       
        .searchbox input{
            height: 100%;
            border: none;
            background: transparent;
            text-indent: 20px;
            font-size: 18px;
            caret-color: white;
            color: white;
            width: 100%;
        }
        .searchbox button{
            width: 60px;
            min-width: 60px;
            height: 100%;
            border: none;
            background: rgb(50, 54, 57);
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 15px;
            box-sizing: border-box;
            cursor: pointer;
        }

        .rightnav{
            min-width: 150px;
            gap: 20px;
            width: 200px;
            display: flex;
            justify-content:flex-end;
            align-items: center;
            padding: 0 10px;
        }
        .leftnav button img{
            height: 100%;
        }

        .navbutton{
            height: 40px;
            aspect-ratio: 1;
            border: none;
            border-radius: 50%;
            background: transparent;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 8px;
            cursor: pointer;
        }
        .navbutton img{
            width: 100%;
            aspect-ratio: 1;
        }
        .searchbutton{
            display: none;
            /* background: red; */
        }
        
        .navbutton:hover{
            background: rgb(50, 54, 57);
        }
        @media screen and (max-width:600px){
            .searchbutton{
                display: flex;
                padding: 5px;
            }
            .searchbox{
                display: none;
            }
            .centernav{
                padding: 0px;
            }
        }

