.body{
    position:absolute;
    padding: 130px 15px 0px 100px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 20px;
    row-gap: 30px;
}
.hometags{
    width: 100%;
    position: fixed;
    display: flex;
    gap: 20px;
    background: black;
    padding: 15px;
    top: 50px;
    left: 80px;
    overflow-x: scroll;
}
.hometags::-webkit-scrollbar{
    height: 0;
    width: 0;
}
.hometags span{
    color: white;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background: rgb(50, 54, 57);
    padding: 5px 10px;
}
.hometags span:hover{
    background: rgb(99, 87, 87);
}
@media screen and (max-width:1000px) {
    .body{
        grid-template-columns: 1fr 1fr  ;
    }
}
@media screen and (max-width:600px) {
    .body{
        grid-template-columns: auto  ;
    padding: 130px 15px 0px 15px;

    }
    .hometags{
    left: 0px;

    }

}