.homecardbody{
    width:     100%;
    min-width: 100%;
    aspect-ratio: 2/1;
    color: white;
}
.homecardbody > img{
    width: 100%;
    aspect-ratio: 2/1;
    border-radius: 20px;
}

.homecarddeltails{
    display: flex;
    justify-content: space-between;
    margin-top: 5px;
}
.homecarddeltails > span{
    display: flex;

}
.homecarddeltails img{
    width: 40px;
    max-height: 40px;
    border-radius: 50%;
}
.homecarddeltails p{
    line-height: 5px;
}

.homecarddeltailstext{
    margin-left: 10px;
    margin-top: -15px;
}

.homecarddeltailstextdescription p{
    color: rgb(160, 160, 160);
}
.homecarddeltailstextdescription p:first-child:hover{
    color: white ;
}