.videopagebody{
    padding: 70px 15px 100px 100px;
    display: flex;
    gap: 20px;
}
.videopagebodyleft{
    max-width: 60vw;
}
.videobox{
    /* height:420px; */
    width: 100%;
    aspect-ratio: 16/9;
    display: flex;
    background: rgb(0, 0, 0);
    justify-content: center;
}
.videochanneldetails{
    height: 100px;
    color: white;
}
.videochanneldetails > div{
    height: 50px;
    display: flex;
    align-items: center;
}
.videochanneldetails >div>img{
    height: 40px;
    aspect-ratio: 1;
    border-radius: 50%;
}
.videochanneldetails div pre{
    margin: 0;
    margin-left: 20px;
    line-height: 10px;
    
}
.videochanneldetails > div>pre>h4{
    color: grey;
}

.videomoredetails{
    background: rgba(128, 128, 128, 0.326);
    margin-top: 20px;
    padding: 10px 20px;
    border-radius: 15px;
}
.videomoredetails pre h4{
    color: white !important;
}

@media screen and (width<900px) {
    .videopagebody{
        flex-direction: column;
        gap: 100px;
    }
    .videopagebodyleft{
        max-width: 100%;
    }
}
@media screen and (width<600px) {
    .videopagebody{
        flex-direction: column;
        padding: 70px 15px 10px 15px;
        gap: 100px;
    }
    
}