.fsmbg{
    height: 100vh;
    width: 100%;
    background: rgba(0, 0, 0, 0.4);
    position: fixed;
    z-index: 15;
    display: none;
    transition: 0.6s;
}
.smhr{
    width: 80%;
}
.Floatingsidemenubody{
    background-color: black;
    height: 100vh;
    width: 250px;
    position: fixed;
    top: 0;
    z-index: 20;
    transition: 0.6s;
    transform: translateX(-250px);
}
.Floatingsidemenubodytop{
    transform: translateY(-16px);
}
.Floatingsidemenubody2{
    display: flex;
    flex-direction: column;
    width: 250px;
    row-gap: 10px;
}
.floatingsideicons{
    display: flex;
    background: transparent;
    border: none;
    width: 250px;
    height: 50px;
    align-items: center;
    gap: 30px;
    border-radius: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 25px;
    cursor: pointer;
}
.floatingsideicons:hover{
    background-color: rgb(50, 54, 57);
}
.floatingsideicons svg{
    height: 100%;
}
.floatingsideicons p{
    color: white;
    font-size: 20px;
}